"use client";

import { useState } from "react";
import axios from "axios";

export default function Home() {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [loading, setLoading] = useState(false);

  const askQuestion = async () => {
    setLoading(true);
    setAnswer("");
    try {
      const response = await axios.get(
        `${process.env.NEXT_PUBLIC_BACKEND_URL || "http://localhost:8000"}/ask`,
        {
          params: { q: question },
        }
      );
      setAnswer(response.data.answer || response.data.error || "No answer.");
    } catch (err) {
      setAnswer("⚠️ Error contacting backend.");
    }
    setLoading(false);
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-6">
      <h1 className="text-3xl font-bold mb-4">Ask the AI!</h1>
      <div className="w-full max-w-md">
        <input
          type="text"
          className="w-full p-2 border border-gray-300 rounded mb-4"
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
          placeholder="Type your question..."
        />
        <button
          className="bg-blue-600 text-white px-4 py-2 rounded w-full"
          onClick={askQuestion}
          disabled={loading}
        >
          {loading ? "Thinking..." : "Ask"}
        </button>
        {answer && (
          <div className="mt-6 p-4 bg-gray-100 border border-gray-300 rounded">
            <strong>Answer:</strong>
            <p>{answer}</p>
          </div>
        )}
      </div>
    </main>
  );
}
